<template>
  <div :class="level > 0 ? 'pl-spacing-300' : ''">
    <div class="relative">
      <div
        class="truncate py-spacing-50 text-[0.9375rem] text-body/m/relaxed/400 text-content-on-neutral-primary"
      >
        <span
          @click="handleNodeClick"
          @click.right.prevent="handleRightClick"
          :class="categoryHasChild || isProduct ? 'cursor-pointer' : ''"
        >
          <span
            v-if="categoryHasChild"
            class="mr-spacing-50 inline-block cursor-pointer"
          >
            <i
              v-if="isExpanded"
              class="fa-solid fa-caret-down h-spacing-200 w-spacing-200 text-content-on-neutral-secondary"
            />
            <i
              v-else
              class="fa-solid fa-caret-right h-spacing-200 w-spacing-200 text-content-on-neutral-secondary"
            />
          </span>
          <span
            v-else
            class="mr-spacing-50 inline-block h-spacing-200 w-spacing-200"
          />
          <span
            class="mr-spacing-100 text-body/s/relaxed/500 text-content-on-neutral-tertiary"
            >{{ getNumbering }}</span
          >
          <i
            v-if="isProduct"
            class="fa-brands fa-pinterest scale-[1.2] rounded-full bg-constant-neutral-white text-[0.9375rem] text-constant-status-error"
          />
          <i
            v-else-if="isCategory && isExpanded"
            class="fa fa-folder-open text-[0.9375rem] text-yellow-500 dark:text-yellow-200"
          />
          <i
            v-else-if="isCategory && !isExpanded"
            class="fa fa-folder text-[0.9375rem] text-yellow-500 dark:text-yellow-200"
          />
        </span>
        <span
          class="pl-spacing-100"
          :class="categoryHasChild || isProduct ? 'cursor-pointer' : ''"
          :title="node?.title"
          @click="handleNodeClick"
          @click.right.prevent="handleRightClick"
          v-html="$highlightMatch(node?.title, searchItemName)"
        />
        <GeneralCheckbox
          v-if="isProduct"
          @click.native="
            handleSelectProduct({
              _id: node?._id,
              originalTitle: node?.title,
            })
          "
          class="relative top-spacing-25 mx-spacing-150 inline-block"
          :selected="
            selectedProducts.some((product) => product?._id === node?._id)
          "
        />
      </div>
    </div>

    <!-- Render child nodes recursively with draggable -->
    <div v-if="isCategory">
      <div v-show="isExpanded">
        <div v-for="(child, index) in node?.commercialItems" :key="child?._id">
          <SelectableTreeView
            :node-obj="child"
            :level="Number(level + 1)"
            :index="index"
            :parent-numbering="getNumbering"
            :expanded-tree-view="expandedTreeView"
            :class="[
              level === 1 && index === node?.commercialItems?.length - 1
                ? 'mb-spacing-100'
                : 'mb-spacing-50',
            ]"
            :search-item-name="searchItemName"
            @select-product="handleSelectProduct"
            :selected-products="selectedProducts"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SelectableTreeView",
  components: {
    GeneralCheckbox: () => import("@/components/General/Checkbox.vue"),
  },
  props: {
    nodeObj: {
      type: Object,
      required: true,
    },
    level: {
      type: Number,
      default: 0,
    },
    index: {
      type: Number,
      required: true,
    },
    parentNumbering: {
      type: String,
    },
    expandedTreeView: {
      type: Object,
      default: () => ({ expand: false }),
    },
    searchItemName: {
      type: String,
      default: "",
    },
    selectedProducts: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      isExpanded: this.expandedTreeView?.expand,
      node: this.nodeObj,
    };
  },
  mounted() {},
  computed: {
    getNumbering() {
      if (!this.parentNumbering) {
        return this.index + 1 + ".";
      }
      return `${this.parentNumbering}${this.index + 1}.`;
    },
    isProduct() {
      return this.node?.type === "product";
    },
    isCategory() {
      return this.node?.type === "category";
    },

    categoryHasChild() {
      return this.isCategory && this.node?.commercialItems?.length > 0;
    },
  },
  watch: {
    nodeObj: {
      handler() {
        this.node = this.nodeObj;
      },
      deep: true,
      immediate: true,
    },
    expandedTreeView: {
      handler(newValue) {
        this.isExpanded = newValue?.expand;
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    async toggle() {
      this.isExpanded = !this.isExpanded;
    },
    handleNodeClick() {
      if (this.isCategory && this.node.commercialItems?.length > 0) {
        this.toggle();
      }
    },
    handleSelectProduct(selectedProduct) {
      this.$emit("select-product", selectedProduct);
    },
  },
  beforeDestroy() {},
};
</script>
